.header{
    display:flex;
    align-items: center;
    justify-content: space-between;
    padding: 20px;
    position: sticky;
    top: 0;
    z-index: 100;
    background-color: white;
    
}
.header_logo{
    height: 25px;
    margin-left: 20px;
}

.header_input{
    display: flex;
    align-items: center;
    width: 40%;
  border: 1px solid lightgray;


}

.header_icons{
    display: flex;
    align-items: center;
  
}
.header_input > input{
    flex: 1;
    border: none
    ;
 
}
.header_inputbutton{
width: 50px;
background-color: #fafafa;
border-left: 1px solid lightgrey ;
color: grey;
}
.header__icon{
    margin-right: 8px;
    
}
